#modeSwitcher {
  z-index: 11;
}

#container {
  width: 100%;
  height: 100%;
}

.el-select {
  width: 80%;
  margin-top: 40px;
}

#modeSwitcher {
  position: fixed;
  bottom: 20px;
  right: 20px;
}
</style>

